<template>
    <section class="section">
        <div class="main-wrap">
            <main class="main">
                <div class="bg">
                    <PhoneBg></PhoneBg>
       <div class="home-btn"><img src="../assets/btn-home.png" alt=""  @click="go_boost_click"></div>

                    <div class="content-bg">

                        <div class="slide slide_wrap">
                         <div class="slide_item" v-for="(a,i) in " :key="i">
                                <img :src="[i]" alt="">
                            </div>
                            <div class="slide_prev_button slide_button"></div>
                            <div class="slide_next_button slide_button"></div>
                        </div>




                    </div>


                </div>
            </main>
        </div>
    </section>
</template>

<script>
import PhoneBg from './PhoneBg.vue';
import boostimg from "../js/boostimg";
export default {
    name: 'images',
    components: {
        PhoneBg,
    },
    data() {
        return {
            : boostimg[0].img,
        }
    },
mounted() {
    this.images_slide();
},
    methods: {
    go_boost_click() {
      var audio = new Audio(require('../assets/main/sound-2.mp3'));
      audio.play();
      setTimeout(() => {
        location.href = '/#/newjeans/boost';
      }, 1000)
    },
        images_slide() {
         //   (width )
const slide = document.querySelector(".slide");
let slideWidth = slide.clientWidth;

//   
const prevBtn = document.querySelector(".slide_prev_button");
const nextBtn = document.querySelector(".slide_next_button");

//         
let slideItems = document.querySelectorAll(".slide_item");
//          
const maxSlide = slideItems.length;

//          
let currSlide = 1;


//    start, end  
const startSlide = slideItems[0];
const endSlide = slideItems[slideItems.length - 1];
const startElem = document.createElement("div");
const endElem = document.createElement("div");

endSlide.classList.forEach((c) => endElem.classList.add(c));
endElem.innerHTML = endSlide.innerHTML;

startSlide.classList.forEach((c) => startElem.classList.add(c));
startElem.innerHTML = startSlide.innerHTML;

//    
slideItems[0].before(endElem);
slideItems[slideItems.length - 1].after(startElem);

//         
slideItems = document.querySelectorAll(".slide_item");
//
let offset = slideWidth + currSlide;
slideItems.forEach((i) => {
  i.setAttribute("style", `left: ${-offset}px`);
});

function nextMove() {
  currSlide++;
  //       
  if (currSlide <= maxSlide) {
    //    offset 
    const offset = slideWidth * currSlide;
    //    left offset 
    slideItems.forEach((i) => {
      i.setAttribute("style", `left: ${-offset}px`);
    });

  } else {
    //    - currSlide          
    currSlide = 0;
    let offset = slideWidth * currSlide;
    slideItems.forEach((i) => {
      i.setAttribute("style", `transition: ${0}s; left: ${-offset}px`);
    });
    currSlide++;
    offset = slideWidth * currSlide;
    //    left offset 
    setTimeout(() => {
      //    left offset 
      slideItems.forEach((i) => {
        // i.setAttribute("style", `transition: ${0}s; left: ${-offset}px`);
        i.setAttribute("style", `transition: ${0.15}s; left: ${-offset}px`);
      });
    }, 0);
 
  }
}
function prevMove() {
  currSlide--;
  // 1      
  if (currSlide > 0) {
    //    offset 
    const offset = slideWidth * currSlide;
    //    left offset 
    slideItems.forEach((i) => {
      i.setAttribute("style", `left: ${-offset}px`);
    });

  } else {
    //    - currSlide          
    currSlide = maxSlide + 1;
    let offset = slideWidth * currSlide;
    //    left offset 
    slideItems.forEach((i) => {
      i.setAttribute("style", `transition: ${0}s; left: ${-offset}px`);
    });
    currSlide--;
    offset = slideWidth * currSlide;
    setTimeout(() => {
      //    left offset 
      slideItems.forEach((i) => {
        // i.setAttribute("style", `transition: ${0}s; left: ${-offset}px`);
        i.setAttribute("style", `transition: ${0.15}s; left: ${-offset}px`);
      });
    }, 0);  
  }
}

//     
nextBtn.addEventListener("click", () => {
  //       
  nextMove();
});
//     
prevBtn.addEventListener("click", () => {
  //       
  prevMove();
});

//      slideWidth  
window.addEventListener("resize", () => {
  slideWidth = slide.clientWidth;
});

// ()    
let startPoint = 0;
let endPoint = 0;

// PC   ()
slide.addEventListener("mousedown", (e) => {
  startPoint = e.pageX; //     
});

slide.addEventListener("mouseup", (e) => {
  endPoint = e.pageX; //     
  if (startPoint < endPoint) {
    //     
    prevMove();
  } else if (startPoint > endPoint) {
    //     
    nextMove();
  }
});

//    ()
slide.addEventListener("touchstart", (e) => {
  startPoint = e.touches[0].pageX; //    
});
slide.addEventListener("touchend", (e) => {
  endPoint = e.changedTouches[0].pageX; //    
  if (startPoint < endPoint) {
    //    
    prevMove();
  } else if (startPoint > endPoint) {
    //    
    nextMove();
  }
});

//    
// let loopInterval = setInterval(() => {
//   nextMove();
// }, 3000);

// //      
// slide.addEventListener("mouseover", () => {
//   clearInterval(loopInterval);
// });

// //      
// slide.addEventListener("mouseout", () => {
//   loopInterval = setInterval(() => {
//     nextMove();
//   }, 3000);
// });

        }
    },


}


</script>
<style scoped>
.home-btn {
  position: absolute;
  top: 120px;
  left: 52px;
  z-index: 1;
  width: 33px;
  height: 33px;
}

.home-btn>img {
  width: 100%;
  cursor: pointer;
}
.content-bg {
    position: absolute;
    top: 40px;
    bottom: 46px;
    left: 36px;
    right: 37px;
    overflow: hidden;
    background: black;
    text-align: center;
    padding-top: 100px;
}

.slide {
    /* layout */
    display: flex;
    flex-wrap: nowrap;
    /*    (width, height)       hidden . */
    overflow: hidden;

    /* position */
    /* slide_button position absolute   top, left, right offset    relative . ( static, static        html  offset  ) */
    position: relative;

    /* size */
    width: 100%;

    /* slide drag  DOM    */
    user-select: none;
}

.slide_item {
    /* layout */
    display: flex;
    align-items: center;
    justify-content: center;

    /* position -   left offset   */
    position: relative;
    left: 0px;

    /* size */
    width: 100%;
    height: 560px;
    /* flex item flex-shrink  1    ,      0 . */
    flex-shrink: 0;

    /* transition */
    transition: left 0.15s;
}

.slide_button {
    /* layout */
    display: flex;
    justify-content: center;
    align-items: center;

    /* position */
    position: absolute;
    /*      */
    top: calc(50% - 16px);

    /* size */
    width: 32px;
    height: 32px;

    /* style */
    border-radius: 100%;
    cursor: pointer;
}

.slide_prev_button {
  left: 10px;
  background-image: url('../assets/movingphotos/ico-prev.png');
  opacity: 0.3;
  height: 40px;
  width: 40px;
}

.slide_next_button {
  right: 10px;
    opacity: 0.3;

  background-image: url('../assets/movingphotos/ico-next.png');
  height: 40px;
  width: 40px;
}
.slide_item>img {
width: 100%;
  -webkit-user-drag: none;
}

/*   */
ul,
li {
    list-style-type: none;
    padding: 0;
    margin: 0;
}



.slide_item_duplicate {
    display: flex;
    align-items: center;
    justify-content: center;
    position: relative;
    left: 0px;
    width: 100%;
    height: 300px;
    flex-shrink: 0;
    transition: left 0.15s;
}
@media screen and (max-width: 500px) {
  .section{
    height: 0;
  }
  .main{
width: 100%;
height:700px;
  }
.slide_item{
  width: 100%;
}
.slide_item>img{
width: 100%;
}
.content-bg{
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  width: 100%;
padding-top: 70px;
}
.slide{
  width: 100%;
}

}
</style>