<template>
  <section class="section">
    <div class="main-wrap">
      <main class="main">
        <div class="bg">
          <PhoneBg_home></PhoneBg_home>

          <div class="content-bg">
            <div class="slide slide_wrap">
              <div class="slide_item"  draggable="true" >
                <img :src="[].img" alt=""  @dragover="handleDrag" draggable="true" />
              </div>
              <div class="slide_item"  draggable="true" >
                <img :src="[].img" alt=""   @dragover="handleDrag" draggable="true" />
              </div>
              <div class="slide_item"  draggable="true" >
                <img :src="[].img" alt="" @dragover="handleDrag" draggable="true" />
              </div>
              <div class="slide_item"  draggable="true" >
                <img :src="[].img" alt="" @dragover="handleDrag" draggable="true" />
              </div>
              <div class="slide_item"  draggable="true" >
                <img :src="[]?.img" alt=""   @dragover="handleDrag" draggable="true" />
              </div>
              <div class="slide_prev_button slide_button"></div>
              <div class="slide_next_button slide_button"></div>
            </div>

            <div class="popup" v-if=" == 0">
              <ul>
                <li><img src="../assets/movingphotos/arrow.png" alt="arrow">
                  <img src="../assets/movingphotos/hand.png" alt="hand"></li>
                <li>      <br>
                     <br> <br>
                  Swipe up and down to see <br>
                  photos of the other members. <br>
                </li>
              </ul>
            </div>

          </div>

        </div>
      </main>
    </div>
  </section>
</template>
<script>
//import $ from 'jquery';
import PhoneBg_home from './PhoneBg_home.vue';
import MoveHi from "../js/MoveHi";
import MoveMj from "../js/MoveMj";
import MoveHn from "../js/MoveHn";
import MoveDn from "../js/MoveDn";
import MoveHr from "../js/MoveHr";
export default {
  name: 'MovingPhotos',
  components: {
    PhoneBg_home,
  },
  data() {
    return {
      : MoveMj,
      : MoveHn,
      : MoveHi,
      : MoveDn,
      : MoveHr,
      : 100,
      : 0,
    }
  },
  methods: {

    handleDrag(e) {
        e.preventDefault();
        const width = e.target.offsetWidth;
        const x = e.offsetX;
        const ratio = x / width;
        const totalImages = this[e.target.alt].length;
        const index = Math.floor(ratio * totalImages);
        this. = index;
    }

  }, // Method End
created() {
  setTimeout(() => {
  this. = 1;
  }, 3000)

},
  mounted() {
    //   (width )
    const slide = document.querySelector(".slide");
    let slideHeight = slide.clientHeight;

    //   
    const prevBtn = document.querySelector(".slide_prev_button");
    const nextBtn = document.querySelector(".slide_next_button");

    //         
    const slideItems = document.querySelectorAll(".slide_item");
    //          
    const maxSlide = slideItems.length;

    //          
    let currSlide = 1;


    function nextMove() {
      currSlide++;
      //       
      if (currSlide <= maxSlide) {
        //    offset 
        const offset = slideHeight * (currSlide - 1);
        //    left offset 
        slideItems.forEach((i) => {
          i.setAttribute("style", `top: ${-offset}px`);
        });

      } else {
        currSlide--;
      }
    }
    function prevMove() {
      currSlide--;
      // 1      
      if (currSlide > 0) {
        //    offset 
        const offset = slideHeight * (currSlide - 1);
        //    left offset 
        slideItems.forEach((i) => {
          i.setAttribute("style", `top: ${-offset}px`);
        });

      } else {
        currSlide++;
      }
    }

    //     
    nextBtn.addEventListener("click", () => {
      //       
      nextMove();
    });
    //     
    prevBtn.addEventListener("click", () => {
      //       
      prevMove();
    });

    //      slideWidth  
    window.addEventListener("resize", () => {
      slideHeight = slide.clientHeight;
    });
  },
} //default End
</script>

<style scoped>
.content-bg {
  position: absolute;
  top: 40px;
  bottom: 46px;
  left: 36px;
  right: 37px;
  overflow: hidden;
  background: black;
  text-align: center;
  padding-top: 60px;
}

.list-wrap1 {
  height: 661px;
}

.list-wrap1>img {
  height: 100%;

}

.slide {
  display: flex;
  flex-wrap: nowrap;
  overflow: hidden;
  position: relative;
  height: 661px;
  user-select: none;
  flex-direction: column;
}

.slide_item {
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
  height: 661px;
  flex-shrink: 0;
  transition: top 0.15s;
}

.slide_item>img {
  height: 661px;
  -webkit-user-drag: none;
  transition: 1s;
}

.slide_button {
  /* layout */
  display: flex;
  justify-content: center;
  align-items: center;

  /* position */
  position: absolute;
  /*      */
  top: calc(50% - 16px);

  /* size */
  width: 32px;
  height: 32px;

  /* style */
  border-radius: 100%;
  cursor: pointer;
  color: aliceblue;
}

.slide_prev_button {
  left: 10px;
  background-image: url('../assets/movingphotos/ico-prev.png');
  height: 40px;
  width: 40px;
}

.slide_next_button {
  right: 10px;
  background-image: url('../assets/movingphotos/ico-next.png');
  height: 40px;
  width: 40px;

}

/*   */
ul,
li {
  list-style-type: none;
  padding: 0;
  margin: 0;
}


.popup {
  width: 290px;
  height: 120px;
  background: rgba(0, 0, 0, 0.808);
  position: absolute;
  bottom: 140px;
  left: 11%;
  border-radius: 20px;
}

.popup>ul {
  display: flex;
  flex-direction: row;
  justify-content: center;
  height: 100%;
  align-items: center;
  gap: 10px;
}

.popup>ul>li {
  color: #fff;
  font-size: 11px;
  font-weight: 600;
  line-height: 14px;
}

.popup>ul>li:nth-child(1){
  display: flex;
  flex-direction: column;
  align-items: center;
}

.popup>ul>li img:nth-child(1) {
  width: 47px;
}

.popup>ul>li:nth-child(3) {
  flex: 0.8;
}
@media screen and (max-width: 500px) {
.slide_item{
  width: 100%;
  height: 100%;
}
.slide_item>img{
height: auto;
width: 100%;
}
.content-bg{
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  width: 100%;
  height: 100%;
  padding-top: 0;
}
.slide{
  width: 100%;
  height: 100%;
}
}
</style>